import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import {
	LoadingController,
	MenuController,
	ModalController,
} from "@ionic/angular";
import { Location } from "@angular/common";
import axios from "axios";
import { Subscription } from "rxjs";
import { User } from "src/app/model/user.model";
import { SolicitudService } from "src/app/services/solicitud.service";
import { UserService } from "src/app/services/user.service";
import { API } from "src/environments/environment";
import { SuccessModalComponent } from "src/app/shared/success-modal/success-modal.component";

type IconType = "engine" | "time" | "heart" | "diamond";
@Component({
	selector: "app-eval-prof",
	templateUrl: "./eval-prof.page.html",
	styleUrls: ["./eval-prof.page.scss"],
})
export class EvalProfPage implements OnInit {
	grabbedUser: User;
	userSub: Subscription;
	selectedIcons: IconType[] = [];
	currentRate: number = 1;
	error;
	comment;
	comment_bad_services = "";
	isTouched = false;

	constructor(
		private modalController: ModalController,
		private router: Router,
		private userService: UserService,
		private menuController: MenuController,
		private requestService: SolicitudService,
		private loadingController: LoadingController,
		private location: Location
	) {}

	ngOnInit() {
		this.userSub = this.userService.loggedUser.subscribe((user) => {
			this.grabbedUser = user;
		});
	}

	ionViewWillEnter() {
		this.menuController.enable(true, "user");
	}

	openMenu() {
		this.menuController.open();
	}

	isSelectedIcon(type: IconType) {
		return this.selectedIcons.includes(type);
	}

	setCurrentRate(rate: number) {
		this.currentRate = rate;
		this.isTouched = true;
	}

	setSelectedIcon(type: IconType) {
		if (this.selectedIcons.includes(type)) {
			this.selectedIcons = this.selectedIcons.filter(
				(currentType: IconType) => type !== currentType
			);
		} else {
			this.selectedIcons.push(type);
		}
	}

	async rateProfessional() {
		try {
			let loader = await this.loadingController.create({
				message: "Enviando calificaci√≥n...",
			});
			loader.present();
			let body = {
				request_services_id: this.requestService.solicitud.solicitudID,
				technical_capacity: this.selectedIcons.includes("engine") ? "1" : "0",
				puntuality: this.selectedIcons.includes("time") ? "1" : "0",
				cordiality: this.selectedIcons.includes("heart") ? "1" : "0",
				service_and_satisfaction: this.selectedIcons.includes("diamond")
					? "1"
					: "0",
				stars: this.currentRate.toString(),
				comment: this.comment,
				comment_bad_services: this.comment_bad_services,
			};
			let response = await axios.post(`${API}/supplier/evaluation`, body, {
				headers: {
					Authorization: `Bearer ${this.grabbedUser.access_token}`,
				},
			});
			await loader.dismiss();
			if (response.data && response.data.code && response.data.code !== 200) {
				this.error = "Ocurri√≥ un error al enviar la calificaci√≥n";
			} else {
				this.confirmRequest();
			}
		} catch (error) {
			console.log(error);
			this.error = "Ocurri√≥ un error al enviar la calificaci√≥n.";
		}
	}

	confirmRequest() {
		this.modalController
			.create({
				component: SuccessModalComponent,
				componentProps: {
					message: "¬°GRACIAS POR TU EVALUACI√ìN!",
					redirect: true,
					redirectUrl: "/user/solicitudes",
				},
				cssClass: "modalSuccess",
			})
			.then((modalEl) => {
				modalEl.present();
			});
	}

	goBack() {
		this.location.back();
	}
}
